<template>
  <div>
    <div class="RightContent">
      <div class="ArticleList">
        <div style="margin-bottom:10px">
          <el-button type="primary" @click="OnOpenDialog()" plain></el-button>
        </div>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.FriendUrlNickName"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.FriendUrlAdress"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.FriendUrlIcoUrl"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.FriendUrlDescript"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.order"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="OnDialogCancel()"> </el-button>
            <el-button type="primary" @click="OnDialogSubmit()"> </el-button>
          </div>
        </el-dialog>

        <!---->
        <el-table :data="FriendUrlList" style="width: 100%" border :header-cell-style="{background:'#f7f7f7'}">
          <el-table-column prop="FriendUrlNickName" label=""></el-table-column>
          <el-table-column prop="FriendUrlAdress" label=""></el-table-column>
          <el-table-column prop="FriendUrlIcoUrl" label="" width="300" :show-overflow-tooltip="true"></el-table-column>
          <el-table-column prop="FriendUrlDescript" label=""></el-table-column>
          <el-table-column prop="order" label="0"></el-table-column>
          <el-table-column fixed="right" label="" width="130">
            <template slot-scope="scope">
              <el-button @click="EditFriendUrl(scope.row)" type="text" size="small" class="warning-color"></el-button>
              <el-button @click="DeleteFriendUrl(scope.row._id)" type="text" size="small" class="danger-color"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!---->
        <div v-if="FriendUrlTotal>10">
          <el-pagination layout="prev, pager, next"
                         :total=FriendUrlTotal
                         :page-size=PagiSize
                         @current-change="ChangeCurPage"
                         @next-click="NextPage"
                         @prev-click="NextPage">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TagIndex",
  data: function () {
    return {
      FriendUrlList: [],
      dialogFormVisible: false,
      form: {
        FriendUrlNickName: '',
        FriendUrlAdress: '',
        FriendUrlIcoUrl: '',
        FriendUrlDescript: ''
      },
      formLabelWidth: '80px',

      FriendUrlTotal: 0,
      PagiSize: 10,
      MyCurPage: 1
    }
  },
  methods: {
    /**/
    OnOpenDialog: function () {
      // 
      this.form = {};
      this.dialogFormVisible = true;
    },
    /**/
    OnDialogSubmit: function () {
      var That = this;
      if (this.form.FriendUrlDescript) {
        this.SQAjax({
          Url: '/api/FriendUrlEditor/backend',
          RequestData: That.form,
          Success: function (data) {
            if (That.form._id) {
              delete That.form._id;
            }
            That.SkipTo(That.MyCurPage);
          }
        });

        this.dialogFormVisible = false;
      }
    },
    OnDialogCancel: function () {
      if (this.form._id) {
        delete this.form._id;
      }
      this.dialogFormVisible = false;
    },
    /**/
    GetData: function () {
      var That = this;
      That.SQAjax({
        Url: '/api/FriendUrlRead/foreend',
        RequestData: {
          PagnationData: {
            Skip: 0,
            Limit: 11
          }
        },
        Success: function (data) {
          if (data.length > 10) {
            data.pop();

            That.SQAjax({
              Url: '/api/getfriendurlnum',
              Success: function (data) {
                That.FriendUrlTotal = data;
              }
            });
          }

          That.FriendUrlList = data;
        }
      });
    },
    /**/
    DeleteFriendUrl: function (Id) {
      var That = this;

      That.SQAjax({
        Url: '/api/FriendUrlDelete/backend',
        RequestData: {
          _id: Id
        },
        Success: function (data) {
          That.$message('');
          That.SkipTo(That.MyCurPage);
        }
      });
    },
    /**/
    EditFriendUrl: function (RowData) {
      this.OnOpenDialog();
      this.form = RowData;
    },
    // 
    ChangeCurPage: function (CurPage) {
      this.SkipTo(CurPage);
      this.MyCurPage = CurPage;
    },
    NextPage: function (CurPage) {
      this.SkipTo(CurPage);
      this.MyCurPage = CurPage;
    },
    SkipTo: function (CurPage) {
      var That = this;
      That.SQAjax({
        Url: '/api/FriendUrlRead/foreend',
        RequestData: {
          PagnationData: {
            Skip: (CurPage - 1) * 10,
            Limit: 10
          }
        },
        Success: function (data) {
          That.FriendUrlList = data;
        }
      });
    }
  },
  mounted: function () {
    this.GetData();
    this.bus.$emit('Topbar', {
      MenuHighLight: '5'
    });
  }
}
</script>

<style scoped>

</style>



