<template>
  <div>
    <div class="RightContent">
      <div class="ArticleList">
        <div style="margin-bottom:10px">
          <el-button type="primary" @click="OpenCreateDialog()" plain></el-button>
        </div>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.TextContent"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-date-picker v-model="form.CreateDate" format="yyyy  MM  dd " value-format="yyyy-MM-dd" type="date" placeholder=""></el-date-picker>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="CloseCreateDialog()"> </el-button>
            <el-button type="primary" @click="PostTimeLineData()"> </el-button>
          </div>
        </el-dialog>

        <!---->
        <el-table :data="TimeLineList" style="width: 100%" border :header-cell-style="{background:'#f7f7f7'}">
          <el-table-column prop="TextContent" label=""></el-table-column>
          <el-table-column prop="CreateDate" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="130">
            <template slot-scope="scope">
              <el-button @click="EditTag(scope.row._id,scope.row.TagName,scope.row.TagNo)" type="text" size="small" class="warning-color"></el-button>
              <el-button @click="Delete(scope.row._id)" type="text" size="small" class="danger-color"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TimeLine",
    data:function () {
      return {
        TimeLineList:'',
        dialogFormVisible:false,
        formLabelWidth: '80px',
        form:{
          TextContent:'',
          CreateDate:new Date()
        },
      }
    },
    methods:{
      // 
      OpenCreateDialog:function(){
        this.dialogFormVisible = true;
      },
      // 
      CloseCreateDialog:function(){
        this.dialogFormVisible = false;
      },
      // 
      PostTimeLineData:function(){
        var That = this;

        if(this.form.TextContent && this.form.CreateDate){
          this.SQAjax({
            Url:'/api/TimeLineCreate/backend',
            RequestData:{
              TextContent: this.form.TextContent,
              CreateDate:this.form.CreateDate
            },
            Success:function () {
              That.GetTimeLineList();
              That.CloseCreateDialog();
            }
          });
        }
      },
      // 
      GetTimeLineList:function () {
        var That = this;
        That.SQAjax({
          Url:'/api/TimeLineRead/foreend',
          Success:function (data) {
            data.forEach(function (Item,I) {
              Item.CreateDate = Item.CreateDate.slice(0,10);
            });
            That.TimeLineList = data;
          }
        });
      },
      /**
       * 
       * @param CommentId id
       * @param ArticleId id
       * @constructor
       */
      Delete:function (Id) {
        var That = this;

        That.SQAjax({
          Url:'/api/TimeLineDelete/backend',
          RequestData:{
            _id:Id
          },
          Success:function (data) {
            That.$message('');
            That.GetTimeLineList();
          }
        });
      },
    },
    mounted:function () {
      this.GetTimeLineList();
      this.bus.$emit('Topbar',{
        MenuHighLight:'3'
      });
    }
  }
</script>

<style scoped>

</style>



