<template>
  <div>
    <div class="RightContent">
      <div class="ArticleList">
        <div style="margin-bottom:10px">
          <el-button type="primary" @click="OpenCreateDialog()" plain></el-button>
        </div>
        
        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.HeartfeltContent"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.HeartfeltWriter"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-date-picker v-model="form.CreateDate" format="yyyy  MM  dd " value-format="yyyy-MM-dd" type="date" placeholder=""></el-date-picker>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="CloseCreateDialog()"> </el-button>
            <el-button type="primary" @click="PostHeartfelt()"> </el-button>
          </div>
        </el-dialog>

        <!---->
        <el-table :data="TimeLineList" style="width: 100%" border :header-cell-style="{background:'#f7f7f7'}">
          <el-table-column prop="HeartfeltContent" label=""></el-table-column>
          <el-table-column prop="HeartfeltWriter" label=""></el-table-column>
          <el-table-column prop="CreateDate" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="130">
            <template slot-scope="scope">
              <el-button @click="UpdateHeartfelt(scope.row._id,scope.row.HeartfeltContent,scope.row.HeartfeltWriter,scope.row.CreateDate,)" type="text" size="small" class="warning-color"></el-button>
              <el-button @click="DeleteHeartfelt(scope.row._id)" type="text" size="small" class="danger-color"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TimeLine",
    data:function () {
      return {
        TimeLineList:'',
        dialogFormVisible:false,
        formLabelWidth: '80px',
        form:{
          HeartfeltContent:'',
          HeartfeltWriter:'',
          CreateDate:new Date()
        },
      }
    },
    methods:{
      // 
      OpenCreateDialog:function(){
        this.form._id = '';
        this.form.HeartfeltContent = '';
        this.form.HeartfeltWriter = '';
        this.form.CreateDate = '';

        this.dialogFormVisible = true;
      },
      // 
      CloseCreateDialog:function(){
        this.dialogFormVisible = false;
      },
      // 
      PostHeartfelt:function(){
        var That = this;

        if(this.form.HeartfeltContent && this.form.CreateDate){
          this.SQAjax({
            Url:'/api/HeartfeltEditor/backend',
            RequestData:this.form,
            Success:function () {
              That.GetHeartfeltList();
              That.CloseCreateDialog();
            }
          });
        }
      },
      //
      UpdateHeartfelt:function(Id,HeartfeltContent,HeartfeltWriter,CreateDate){
        this.form._id = Id;
        this.form.HeartfeltContent = HeartfeltContent;
        this.form.HeartfeltWriter = HeartfeltWriter;
        this.form.CreateDate = CreateDate;

        this.dialogFormVisible = true;
      },
      // 
      GetHeartfeltList:function () {
        var That = this;
        That.SQAjax({
          Url:'/api/HeartfeltRead/foreend',
          Success:function (data) {
            data.forEach(function (Item,I) {
              Item.CreateDate = Item.CreateDate.slice(0,10);
            });
            That.TimeLineList = data;
          }
        });
      },
      // 
      DeleteHeartfelt:function (Id) {
        var That = this;

        That.SQAjax({
          Url:'/api/HeartfeltDelete/backend',
          RequestData:{
            _id:Id
          },
          Success:function (data) {
            That.GetHeartfeltList();
          }
        });
      }
    },
    mounted:function () {
      this.GetHeartfeltList();
      this.bus.$emit('Topbar',{
        MenuHighLight:'4'
      });
    }
  }
</script>

<style scoped>

</style>



