<template>
  <div class="LoginContent">
    <el-form ref="form" :model="form" label-width="60px">
      <div style="text-align: center"><h2>sunq's blog </h2></div>
      <div style="text-align: center;color:#409EFF"><h4></h4></div>
      <el-form-item label="">
        <el-input v-model="form.CnName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.PassWord"></el-input>
      </el-form-item>
      <div style="text-align: center">
        <el-button type="primary" @click="OnLogin()"></el-button>
        <!--<el-button type="primary" @click="OnRegist()"></el-button>-->
      </div>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios'
import { Loading } from 'element-ui'

export default {
  name: "LoginPage",
  data: () => {
    return {
      form: {
        CnName: '',
        PassWord: 'Github'
      }
    }
  },
  methods: {
    OnLogin: function () {
      var That = this;
      var AjaxLoading = Loading.service({ fullscreen: true });
      axios.post('/api/UserReadOne', {
        CnName: this.form.CnName,
        PassWord: this.form.PassWord,
        UserType: 'Admin'
      }).then(function (response) {
        AjaxLoading.close();
        if (response.data.status == '0') {
          That.$message({
            message: '',
            type: 'success',
            duration: 800
          });
          window.localStorage.setItem("SQBlog", JSON.stringify(response.data.data));
          window.localStorage.setItem("SQBlogUser", That.form.CnName);
          That.$router.push({ name: 'Article' });

          // 
          That.createLog({
            moduleType: 'button',
            operateType: '',
            operateContent: ''
          });

          // 
          That.bus.$emit('changeUser', That.form.CnName);
        } else if (response.data.status == '1') {
          That.$message.error('');

          // 
          That.createLog({
            moduleType: 'button',
            operateType: '',
            operateContent: ''
          });
        }
      }).catch(function (error) {
      });
    },
    OnRegist: function () {
      var That = this;
      axios.post('/api/UserCreate', {
        CnName: this.form.CnName,
        PassWord: this.form.PassWord,
        UserType: 'Admin'
      }).then(function (response) {
        if (response.data.status == '200') {
          That.$message({
            message: '',
            type: 'success'
          });
        }
      }).catch(function (error) {
      });
    },
    // enter
    enterKey: function(){
      let that = this;
      const code = event.keyCode ? event.keyCode : (event.which ? event.which : event.charCode);
      
      if(code == 13) that.OnLogin();
    }
  },
  // 
  mounted: function(){
    document.addEventListener('keyup',this.enterKey);
  },
  // enter
  destroyed: function(){
    document.removeEventListener('keyup', this.enterKey);
  }
}
</script>

<style scoped lang="less">
@import "../assets/css/base.less";

@media only screen and (max-device-width: 768px) {
  .LoginContent {
    padding: 0 20px;
    margin: 12rem auto 0;
  }
}

@media only screen and (min-device-width: 768px) {
  .LoginContent {
    width: 600px;
    margin: 12rem auto 0;
  }
}
</style>



